body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.timeline {
  list-style: none;
  padding: 20px;
  position: relative; }
  .timeline:before {
    top: 0;
    bottom: 0;
    position: absolute;
    content: " ";
    width: 3px;
    background-color: #dbdbdb;
    margin-left: -1.5px; }
  .timeline > li {
    margin-bottom: 20px;
    position: relative; }
    .timeline > li:before {
      content: " ";
      display: table; }
    .timeline > li:after {
      content: " ";
      display: table;
      clear: both; }
    .timeline > li:before {
      content: " ";
      display: table; }
    .timeline > li:after {
      content: " ";
      display: table;
      clear: both; }
    .timeline > li > .timeline-panel {
      width: 45%;
      float: left;
      border: 1px solid #ffffff;
      border-radius: 10px;
      position: relative;
      margin: 0;
      padding: 0;
      box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175); }
      .timeline > li > .timeline-panel:before {
        position: absolute;
        top: 12px;
        right: -20px;
        display: inline-block;
        border-top: 10px solid transparent;
        border-left: 7px solid #ccc;
        border-right: 0 solid #ccc;
        border-bottom: 10px solid transparent;
        content: " "; }
      .timeline > li > .timeline-panel:after {
        position: absolute;
        top: 12px;
        right: -19px;
        display: inline-block;
        border-top: 9px solid transparent;
        border-left: 6px solid #fff;
        border-right: 0 solid #fff;
        border-bottom: 9px solid transparent;
        content: " "; }
    .timeline > li > .timeline-badge {
      color: #fff;
      width: 30px;
      height: 30px;
      font-size: 1.4em;
      text-align: center;
      position: absolute;
      top: 8px;
      margin-left: -15px;
      z-index: 100; }
      .timeline > li > .timeline-badge > img {
        height: 100%; }
    .timeline > li.timeline-left > .timeline-panel {
      left: 5%; }
      .timeline > li.timeline-left > .timeline-panel:before {
        border-left-width: 0;
        border-right-width: 15px;
        left: -15px;
        right: auto; }
      .timeline > li.timeline-left > .timeline-panel:after {
        border-left-width: 0;
        border-right-width: 14px;
        left: -14px;
        right: auto; }

.timeline-heading {
  padding: 10px 30px 10px 30px;
  min-height: 40px;
  background-color: white;
  -webkit-border-top-left-radius: 10px;
  -webkit-border-top-right-radius: 10px;
  -moz-border-radius-topleft: 10px;
  -moz-border-radius-topright: 10px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px; }
  .timeline-heading > div {
    width: 25%;
    display: inline-table;
    vertical-align: middle; }
    .timeline-heading > div > img {
      height: 15px;
      margin-right: 5px; }

.timeline-body {
  background-color: #F8F8F8;
  height: 100%;
  padding-left: 30px;
  padding-right: 30px;
  -webkit-border-bottom-right-radius: 10px;
  -webkit-border-bottom-left-radius: 10px;
  -moz-border-radius-bottomright: 10px;
  -moz-border-radius-bottomleft: 10px;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px; }

.table-item {
  width: 100%;
  margin-bottom: 3%; }
  .table-item thead > tr > th {
    border-bottom: none;
    padding: 8px 8px 8px 0px; }
  .table-item tbody > tr > td {
    padding: 8px 8px 8px 0px;
    border-top: 2px solid #f2f2f2; }

.product-description {
  width: 75%; }

@media (max-width: 767px) {
  ul.timeline > li > .timeline-panel {
    width: calc(100% - 90px);
    width: -webkit-calc(100% - 90px);
    margin-left: 5%; }
    ul.timeline > li > .timeline-panel:before {
      border-left-width: 0;
      border-right-width: 15px;
      left: -15px;
      right: auto; }
    ul.timeline > li > .timeline-panel:after {
      border-left-width: 0;
      border-right-width: 14px;
      left: -14px;
      right: auto; }
  .timeline-heading > div {
    margin-right: 5px;
    display: inline;
    text-align: center; } }

body {
  background-color: #EFEFFD; }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

